import { createStore } from "vuex";

export default createStore({
  state: {
    user: {
      avatarUrl: "data:image/jpeg;base64,",
      fullName: "Галь Олександр",
      email: "",
    },
    accessToken: "",
    isLoggedIn: false,
    isUserLoading: false,
  },
  getters: {
    user: ({ user }) => {
      return user;
    },
    accessToken: ({ accessToken }) => {
      return accessToken;
    },
    isLoggedIn: ({ isLoggedIn }) => {
      return isLoggedIn;
    },
  },
  mutations: {
    changeIsLoggedIn(state) {
        state.isLoggedIn = !state.isLoggedIn;
    } 
  },
  actions: {},
});
